import React, { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import MaterialTable from 'material-table';
import Page from '../../../components/Page';
import useMember from '../../../hooks/useMember';
import { Container, Box, IconButton, SvgIcon, makeStyles } from '@material-ui/core';
import { Edit as EditIcon, ArrowRight as ArrowRightIcon, Trash } from 'react-feather';
import DeleteConfirmCheck from '../../../components/DeleteConfirmCheck';
import { useTranslation } from 'react-i18next';
import Header from './Header';
// import Results from './Results';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

export default function MemberList() {
  const { t } = useTranslation();
  const { members, deleteMember, getMemberFullName } = useMember();

  const classes = useStyles();

  const [open, setOpen] = React.useState(false);
  const [needDeleteMember, setDeleteMember] = React.useState({});

  const handleClickOpen = (member) => {
    setOpen(true);
    setDeleteMember(member);
  };

  const handleDeleteMember = async () => {
    await deleteMember(needDeleteMember.id);
    setOpen(false);
    setDeleteMember({});
  };

  const handleClose = () => {
    setOpen(false);
  };

  const columns = [
    {
      title: t('Name'),
      field: 'name',
      render: (rowData) => getMemberFullName(rowData)
    },
    {
      title: t('Phone'),
      field: 'phone'
    },
    {
      title: t('Rank'),
      field: 'rank.name'
    },
    {
      title: t('Gender'),
      field: 'gender',
      render: (rowData) => t(rowData.gender)
    },
    {
      title: t('Chops'),
      field: 'chops',
      render: (rowData) => {
        const chops = rowData.chops.map((chop) => chop.chops || 0);
        if (chops.length === 0) {
          return 0;
        }
        return `${chops.reduce((pref, next) => pref + next)}`;
      }
    },
    {
      title: t('Actions'),
      field: 'actions',
      render: (rowData) => (
        <>
          <IconButton component={RouterLink} to={`/members/${rowData.id}/edit`}>
            <SvgIcon fontSize="small">
              <EditIcon />
            </SvgIcon>
          </IconButton>
          <IconButton onClick={() => handleClickOpen(rowData)}>
            <SvgIcon fontSize="small">
              <Trash />
            </SvgIcon>
          </IconButton>
          <IconButton component={RouterLink} to={`/members/${rowData.id}/detail`}>
            <SvgIcon fontSize="small">
              <ArrowRightIcon />
            </SvgIcon>
          </IconButton>
        </>
      )
    }
  ];

  if (!members) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Member List')}>
      <Container maxWidth={false}>
        <Header />
        {members && (
          <Box mt={3}>
            <MaterialTable title={t('Member List')} columns={columns} data={members} />
          </Box>
        )}
      </Container>
      <DeleteConfirmCheck
        open={open}
        handleClose={handleClose}
        handleDelete={handleDeleteMember}
        checkText={`${needDeleteMember.first_name} ${needDeleteMember.last_name}`}
      />
    </Page>
  );
}
